var obj = {
  tagName: "van-nav-bar",
  props: [
    {
      name: "title",
      desc: "标题",
      type: String,
      value: "",
      default: ""
    },
    {
      name: ":left-arrow",
      desc: "左侧箭头",
      value: false,
      type: Boolean,
      default: false
    }
  ]
};
export default obj;



